.opened {
    visibility: visible;
}

.closed {
    visibility: hidden;
    display: none;
}

.btn-new-product {
    width: 200px;
    background-color: 	#dbdbec;
    height: 40px;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
}

.btn-new-product:hover {
    background-color: 	rgb(197, 195, 230);
}

.product-group {
    margin-top: 30px;
}


